<template>
  <div class="sets">
    <app-sets-item v-for="item in items"
                   :key="item.guid"
                   :item="item"></app-sets-item>
  </div>
</template>

<script>
import StaticService from "@/services/static";
import AppSetsItem from "@/components/SetsItem";

export default {
  name: 'AppSetsList',
  components: {
    AppSetsItem,
  },
  data: () => ({}),
  mounted() {
    this.getTrainers();
    this.getTrainersSets();
  },
  computed: {
    items() {
      return this.$store.state.trainersSets;
    }
  },
  methods: {
    getTrainersSets() {
      StaticService.getTrainersSets()
          .then(res => {
            this.$store.commit('setTrainersSets', res);
          })
    },
    getTrainers() {
      StaticService.getTrainers()
          .then(res => {
            this.$store.commit('setTrainersList', res);
          })
    }
  }
}
</script>

