<template>
  <div class="trainers">
    <app-trainer-item v-for="item in items"
                      :key="item.guid"
                      :item="item"></app-trainer-item>
  </div>
</template>

<script>
import StaticService from "@/services/static";
import AppTrainerItem from "@/components/TrainerItem";

export default {
  name: 'AppTrainersList',
  components: {
    AppTrainerItem
  },
  data: () => ({
  }),
  mounted() {
    this.getTrainers();
  },
  computed: {
    items() {
      return this.$store.state.trainersList;
    }
  },
  methods: {
    getTrainers() {
      StaticService.getTrainers()
          .then(res => {
            this.$store.commit('setTrainersList', res);
          })
    }
  }
}
</script>

